/**
 * Ë∑ØÂæÑÂåπÈÖçÂ∑•ÂÖ∑Á±ª
 * Êèê‰æõÁªü‰∏ÄÁöÑË∑ØÂæÑÂåπÈÖçÂíåÂèÇÊï∞ÊèêÂèñÂäüËÉΩ
 */
export class PathMatcher {
  /**
   * Ë∑ØÂæÑÂåπÈÖç
   */
  static matchPath(pattern: string, path: string): boolean {
    const patternParts = pattern.split("/").filter(Boolean);
    const pathParts = path.split("/").filter(Boolean);

    if (patternParts.length !== pathParts.length) {
      return false;
    }

    for (let i = 0; i < patternParts.length; i++) {
      if (
        patternParts[i] !== pathParts[i] &&
        !patternParts[i].startsWith(":")
      ) {
        return false;
      }
    }

    return true;
  }

  /**
   * ÊèêÂèñË∑ØÂæÑÂèÇÊï∞
   */
  static extractParams(pattern: string, path: string): Record<string, string> {
    const params: Record<string, string> = {};
    const patternParts = pattern.split("/").filter(Boolean);
    const pathParts = path.split("/").filter(Boolean);

    for (let i = 0; i < patternParts.length; i++) {
      if (patternParts[i].startsWith(":")) {
        const paramName = patternParts[i].slice(1);
        params[paramName] = pathParts[i];
      }
    }

    return params;
  }

  /**
   * ËÆ°ÁÆóË∑ØÂæÑÁâπÂºÇÊÄßÂàÜÊï∞
   * Áî®‰∫éË∑ØÁî±ÊéíÂ∫èÔºöÈùôÊÄÅ > Âä®ÊÄÅ(:param) > ÈÄöÈÖçÁ¨¶(*)
   */
  static calculatePathScore(path: string): number {
    const parts = path.split("/").filter(Boolean);
    let score = 0;
    for (const p of parts) {
      if (p === "*")
        score += 1; // ÊúÄÂº±
      else if (p.startsWith(":"))
        score += 2; // ‰∏≠Á≠â
      else score += 3; // ÈùôÊÄÅÊúÄÂº∫
    }
    // Êõ¥ÈïøÁöÑË∑ØÂæÑÊõ¥ÂÖ∑‰ΩìÔºåÁï•ÂæÆÊèêÂçá
    return score * 10 + parts.length;
  }

  /**
   * Âà§Êñ≠‰∏§‰∏™Ë∑ØÂæÑÊòØÂê¶ÂèØËÉΩÂÜ≤Á™Å
   */
  static pathsMayConflict(path1: string, path2: string): boolean {
    const parts1 = path1.split("/").filter(Boolean);
    const parts2 = path2.split("/").filter(Boolean);

    if (parts1.length !== parts2.length) return false;

    for (let i = 0; i < parts1.length; i++) {
      const p1 = parts1[i];
      const p2 = parts2[i];

      // Â¶ÇÊûú‰∏§‰∏™ÈÉ®ÂàÜÈÉΩÊòØÈùôÊÄÅÁöÑ‰∏î‰∏çÂêåÔºåÂàô‰∏ç‰ºöÂÜ≤Á™Å
      if (
        !p1.startsWith(":") &&
        !p1.startsWith("*") &&
        !p2.startsWith(":") &&
        !p2.startsWith("*") &&
        p1 !== p2
      ) {
        return false;
      }

      // Â¶ÇÊûú‰∏Ä‰∏™ÊòØÈÄöÈÖçÁ¨¶ÔºåÂè¶‰∏Ä‰∏™ÊòØÂä®ÊÄÅÂèÇÊï∞ÔºåÂèØËÉΩÂÜ≤Á™Å
      if (
        (p1 === "*" && p2.startsWith(":")) ||
        (p2 === "*" && p1.startsWith(":"))
      ) {
        return true;
      }
    }

    return false;
  }
}
