/**
 * HTMLÊ∏≤ÊüìÂ∑•ÂÖ∑Á±ª
 * Êèê‰æõÁªü‰∏ÄÁöÑHTMLÊ®°ÊùøÁîüÊàêÂäüËÉΩ
 */
export class HtmlRenderer {
  /**
   * ÁîüÊàêÂü∫Á°ÄHTMLÊ®°Êùø
   */
  static generateBaseHtml(
    content: string,
    context: any,
    clientScriptPath: string = "/client.js",
  ): string {
    return `
      <!doctype html>
      <html>
        <head>
          <meta charset="utf-8">
          <title>Vafast SSR App</title>
          <meta name="viewport" content="width=device-width, initial-scale=1">
        </head>
        <body>
          <div id="app">${content}</div>
          <script>
            window.__ROUTE_INFO__ = {
              params: ${JSON.stringify(context.params || {})},
              query: ${JSON.stringify(context.query || {})},
              pathname: '${context.pathname}'
            };
          </script>
          <script type="module" src="${clientScriptPath}"></script>
        </body>
      </html>
    `;
  }

  /**
   * ÁîüÊàêVueÁªÑ‰ª∂HTML
   */
  static generateVueHtml(
    content: string,
    context: any,
    clientScriptPath: string = "/client.js",
  ): string {
    return this.generateBaseHtml(content, context, clientScriptPath);
  }

  /**
   * ÁîüÊàêReactÁªÑ‰ª∂HTML
   */
  static generateReactHtml(
    content: string,
    context: any,
    clientScriptPath: string = "/client.js",
  ): string {
    return `
      <!doctype html>
      <html>
        <head>
          <meta charset="utf-8">
          <title>Vafast SSR App</title>
          <meta name="viewport" content="width=device-width, initial-scale=1">
        </head>
        <body>
          <div id="root">${content}</div>
          <script>
            window.__ROUTE_INFO__ = {
              params: ${JSON.stringify(context.params || {})},
              query: ${JSON.stringify(context.query || {})},
              pathname: '${context.pathname}'
            };
          </script>
          <script type="module" src="${clientScriptPath}"></script>
        </body>
      </html>
    `;
  }
}
