import type { Middleware } from "../types";

/**
 * ÊúçÂä°Âô®Âü∫Á±ª
 * ÂåÖÂê´ÊâÄÊúâÊúçÂä°Âô®Á±ªÂûãÁöÑÂÖ¨ÂÖ±ÈÄªËæë
 */
export abstract class BaseServer {
  protected globalMiddleware: Middleware[] = [];

  use(mw: Middleware) {
    this.globalMiddleware.push(mw);
  }

  /**
   * ÊâìÂç∞ÊâÅÂπ≥ÂåñÂêéÁöÑË∑ØÁî±‰ø°ÊÅØÔºåÁî®‰∫éË∞ÉËØï
   */
  protected logFlattenedRoutes(routes: any[], type: string = "Ë∑ØÁî±"): void {
    console.log(`üöÄ ÊâÅÂπ≥ÂåñÂêéÁöÑ${type}:`);
    for (const route of routes) {
      const method = route.method || "GET";
      const path = route.fullPath || route.path;
      console.log(`  ${method} ${path}`);
      if (route.middlewareChain && route.middlewareChain.length > 0) {
        console.log(`    ‰∏≠Èó¥‰ª∂Èìæ: ${route.middlewareChain.length} ‰∏™`);
      }
    }
    console.log("");
  }

  /**
   * Ê£ÄÊµãË∑ØÁî±ÂÜ≤Á™Å
   * Ê£ÄÊü•ÊòØÂê¶ÊúâË∑ØÂæÑÁõ∏Âêå‰ΩÜÊñπÊ≥ï‰∏çÂêåÁöÑË∑ØÁî±Ôºå‰ª•ÂèäÊΩúÂú®ÁöÑË∑ØÂæÑÂÜ≤Á™Å
   */
  protected detectRouteConflicts(routes: any[]): void {
    const pathGroups = new Map<string, any[]>();

    // ÊåâË∑ØÂæÑÂàÜÁªÑ
    for (const route of routes) {
      const path = route.fullPath || route.path;
      const method = route.method || "GET";
      if (!pathGroups.has(path)) {
        pathGroups.set(path, []);
      }
      pathGroups.get(path)!.push({ ...route, method });
    }

    // Ê£ÄÊü•ÂÜ≤Á™Å
    for (const [path, routeList] of pathGroups) {
      if (routeList.length > 1) {
        const methods = routeList.map((r) => r.method);
        const uniqueMethods = [...new Set(methods)];

        if (uniqueMethods.length === 1) {
          // Áõ∏ÂêåË∑ØÂæÑ„ÄÅÁõ∏ÂêåÊñπÊ≥ï - ËøôÊòØÂÜ≤Á™ÅÔºÅ
          console.warn(
            `‚ö†Ô∏è  Ë∑ØÁî±ÂÜ≤Á™Å: ${uniqueMethods[0]} ${path} ÂÆö‰πâ‰∫Ü ${routeList.length} Ê¨°`,
          );
          routeList.forEach((route, index) => {
            console.warn(`   ${index + 1}. ${route.method} ${path}`);
          });
        } else {
          // Áõ∏ÂêåË∑ØÂæÑ„ÄÅ‰∏çÂêåÊñπÊ≥ï - ËøôÊòØÊ≠£Â∏∏ÁöÑ
          console.log(`‚ÑπÔ∏è  Ë∑ØÂæÑ ${path} ÊîØÊåÅÊñπÊ≥ï: ${uniqueMethods.join(", ")}`);
        }
      }
    }

    // Ê£ÄÊü•ÊΩúÂú®ÁöÑË∑ØÂæÑÂÜ≤Á™ÅÔºàÂä®ÊÄÅË∑ØÁî±ÂèØËÉΩÂÜ≤Á™ÅÔºâ
    this.detectDynamicRouteConflicts(routes);
  }

  /**
   * Ê£ÄÊµãÂä®ÊÄÅË∑ØÁî±ÁöÑÊΩúÂú®ÂÜ≤Á™Å
   */
  private detectDynamicRouteConflicts(routes: any[]): void {
    const dynamicRoutes = routes.filter((r) => {
      const path = r.fullPath || r.path;
      return path.includes(":") || path.includes("*");
    });

    for (let i = 0; i < dynamicRoutes.length; i++) {
      for (let j = i + 1; j < dynamicRoutes.length; j++) {
        const route1 = dynamicRoutes[i];
        const route2 = dynamicRoutes[j];
        const method1 = route1.method || "GET";
        const method2 = route2.method || "GET";

        if (method1 === method2) {
          const path1 = route1.fullPath || route1.path;
          const path2 = route2.fullPath || route2.path;
          // Ê£ÄÊü•Ë∑ØÂæÑÊòØÂê¶ÂèØËÉΩÂÜ≤Á™Å
          if (this.pathsMayConflict(path1, path2)) {
            console.warn(
              `‚ö†Ô∏è  ÊΩúÂú®Ë∑ØÁî±ÂÜ≤Á™Å: ${method1} ${path1} ÂèØËÉΩ‰∏é ${path2} ÂÜ≤Á™Å`,
            );
          }
        }
      }
    }
  }

  /**
   * Âà§Êñ≠‰∏§‰∏™Ë∑ØÂæÑÊòØÂê¶ÂèØËÉΩÂÜ≤Á™Å
   */
  private pathsMayConflict(path1: string, path2: string): boolean {
    const parts1 = path1.split("/").filter(Boolean);
    const parts2 = path2.split("/").filter(Boolean);

    if (parts1.length !== parts2.length) return false;

    for (let i = 0; i < parts1.length; i++) {
      const p1 = parts1[i];
      const p2 = parts2[i];

      // Â¶ÇÊûú‰∏§‰∏™ÈÉ®ÂàÜÈÉΩÊòØÈùôÊÄÅÁöÑ‰∏î‰∏çÂêåÔºåÂàô‰∏ç‰ºöÂÜ≤Á™Å
      if (
        !p1.startsWith(":") &&
        !p1.startsWith("*") &&
        !p2.startsWith(":") &&
        !p2.startsWith("*") &&
        p1 !== p2
      ) {
        return false;
      }

      // Â¶ÇÊûú‰∏Ä‰∏™ÊòØÈÄöÈÖçÁ¨¶ÔºåÂè¶‰∏Ä‰∏™ÊòØÂä®ÊÄÅÂèÇÊï∞ÔºåÂèØËÉΩÂÜ≤Á™Å
      if (
        (p1 === "*" && p2.startsWith(":")) ||
        (p2 === "*" && p1.startsWith(":"))
      ) {
        return true;
      }
    }

    return false;
  }

  /**
   * Ë∑ØÂæÑÂåπÈÖç
   */
  protected matchPath(pattern: string, path: string): boolean {
    const patternParts = pattern.split("/").filter(Boolean);
    const pathParts = path.split("/").filter(Boolean);

    if (patternParts.length !== pathParts.length) {
      return false;
    }

    for (let i = 0; i < patternParts.length; i++) {
      if (
        patternParts[i] !== pathParts[i] &&
        !patternParts[i].startsWith(":")
      ) {
        return false;
      }
    }

    return true;
  }

  /**
   * ÊèêÂèñË∑ØÂæÑÂèÇÊï∞
   */
  protected extractParams(
    pattern: string,
    path: string,
  ): Record<string, string> {
    const params: Record<string, string> = {};
    const patternParts = pattern.split("/").filter(Boolean);
    const pathParts = path.split("/").filter(Boolean);

    for (let i = 0; i < patternParts.length; i++) {
      if (patternParts[i].startsWith(":")) {
        const paramName = patternParts[i].slice(1);
        params[paramName] = pathParts[i];
      }
    }

    return params;
  }
}
