/**
 * ‰æùËµñÁÆ°ÁêÜÂô®
 * Ë¥üË¥£ÊåâÈúÄÂä†ËΩΩÂíåÁÆ°ÁêÜÊ°ÜÊû∂‰æùËµñ
 */
export class DependencyManager {
  private dependencyCache = new Map<string, any>();

  /**
   * ÊåâÈúÄËé∑ÂèñÊ°ÜÊû∂‰æùËµñ
   */
  async getFrameworkDeps(framework: "vue" | "react") {
    if (this.dependencyCache.has(framework)) {
      return this.dependencyCache.get(framework);
    }

    console.log(`üì¶ ÊåâÈúÄÂä†ËΩΩ ${framework} ‰æùËµñ...`);

    try {
      let deps;
      switch (framework) {
        case "vue":
          deps = await Promise.all([
            import("vue"),
            import("@vue/server-renderer"),
          ]);
          break;
        case "react":
          deps = await Promise.all([
            import("react"),
            import("react-dom/server"),
          ]);
          break;
        default:
          throw new Error(`‰∏çÊîØÊåÅÁöÑÊ°ÜÊû∂: ${framework}`);
      }

      this.dependencyCache.set(framework, deps);
      console.log(`‚úÖ ${framework} ‰æùËµñÂä†ËΩΩÂÆåÊàê`);
      return deps;
    } catch (error) {
      console.error(`‚ùå ${framework} ‰æùËµñÂä†ËΩΩÂ§±Ë¥•:`, error);
      throw error;
    }
  }

  /**
   * Ê£ÄÊµãÁªÑ‰ª∂Á±ªÂûã
   */
  detectComponentType(component: any): "vue" | "react" {
    // ÁÆÄÂçïÁöÑÁªÑ‰ª∂Á±ªÂûãÊ£ÄÊµã
    if (component.render && typeof component.render === "function") {
      return "vue";
    }
    if (component.$$typeof) {
      return "react";
    }
    // ÈªòËÆ§‰ΩøÁî® Vue
    return "vue";
  }

  /**
   * Ê∏ÖÈô§ÁºìÂ≠ò
   */
  clearCache(): void {
    this.dependencyCache.clear();
    console.log("üßπ ‰æùËµñÁºìÂ≠òÂ∑≤Ê∏ÖÈô§");
  }

  /**
   * Ëé∑ÂèñÁºìÂ≠òÁä∂ÊÄÅ
   */
  getCacheStatus(): Record<string, boolean> {
    const status: Record<string, boolean> = {};
    for (const [framework] of this.dependencyCache) {
      status[framework] = true;
    }
    return status;
  }
}
